#heading{
    color: rgb(247, 166, 44);
}

.header{
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}
.logo{
    width: 100px    ;
}
.nav-items{
    padding-right: 10px ;
   
    
}
.nav-items ul{
    font-size: 24px;
    display: flex;
    list-style: none;
   
    
    
}
.nav-items ul li{
    margin: 10px;
    padding: 10px;
    margin-top: 0px;
    font-size: 20px;
}
.nav-items ul li img{
    width: 25px;
}
.res-container{
    
    display: flex;
    flex-wrap: wrap;
    margin: 15px;
    
    justify-content:space-evenly;
    
}
.res-container :hover{

    border:1px solid black;
    cursor: pointer;
}
.res-card{
    padding:5px;
    width: 250px;
    
    margin: 5px;
    
   
}



.res-logo {
   width: 100%;
   
   height: 200px;
   
  
}

.search{
    padding: 10px;
}
.filter-btn {
    margin: 10px;
    cursor: pointer;
}
.Shimmer-card{
    margin: 20px;
    width: 250px;
    height: 400px;
    background-color: rgb(206, 204, 204);
    
}
.Shimmer-container{

display: flex;
justify-content: space-evenly;
flex-wrap: wrap;
margin: 15px;
}
.login{
    padding: 0 20px;
